// import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
// import { faSearch } from "@fortawesome/free-solid-svg-icons";
import search from "../assests/searchIcon.svg";

const Search = ({ handleSearchChange }) => {
  return (
    <div className="search p-2 bg-gray-200">
      {/* <div className="nav-text text-xs text-center md:text-start md:w-3/4 md:mx-auto md:pl-1 mt-2">
        <p>
          <span className="text-gray-600">Home</span> &gt;{" "}
          <span className="text-blue-800">Blogs</span>
        </p>
      </div> */}

      <div className="mt-3 p-1 search-section flex flex-col justify-between md:flex-row md:mx-auto">
        <div className="head md:w-1/2 md:px-0 px-2">
          <h1 className="text-xl font-bold text-start">Blogs</h1>
          <p className="text-sm text-start">
            Lorem ipsum dolor sit amet consectetur adipisicing elit. Saepe sed
            pariatur cum ipsum nam et voluptatibus tenetur. Consectetur
            adipisicing elit. Saepe sed pariatur cum ipsum nam et voluptatibus
            tenetur.
          </p>
        </div>
        <div className="search m-2 mt-4 p-2 bg-white rounded-md flex md:m-1 md:h-max">
          <input
            type="text"
            name="text"
            id="text"
            onChange={(e) => {
              handleSearchChange(e);
            }}
            className="p-2 bg-gray-300 rounded-md outline-none h-10 w-full text-black"
            placeholder="Search for an article..."
          />
          <button className=" mx-2 bg-blue-600 h-10 w-12 p-2 rounded-md text-white hover:bg-gray-600 transition-colors">
            {/* <FontAwesomeIcon icon={faSearch} /> */}
            <img src={search} alt="" />
          </button>
        </div>
      </div>
    </div>
  );
};

export default Search;
