import React, { useState, useEffect } from "react";
// import "../paypal/paypal.css";
import book from "../../image/book.jpeg";
import { hosted } from "../../Api";

const ProductDisplay = ({ handleSubmit }) => (
  <section>
    <div className="product">
      <img width={300} src={book} alt="The cover of Stubborn Attachments" />
      <div className="description">
        <h3 className="mt-4">Stubborn Attachments</h3>
        <h5>20Rs</h5>
      </div>
    </div>
    <form>
      <input type="hidden" name="amount" value={30} />
      <input
        type="hidden"
        name="product_name"
        // value="T-Shirt"
      />
      <button
        onClick={(e) => handleSubmit(e)}
        type="submit"
        className="px-4 rounded-1 bg-dark"
      >
        Checkout
      </button>
    </form>
  </section>
);

const Message = ({ message }) => (
  <section>
    <p>{message}</p>
  </section>
);

export const Hosted = () => {
  const [message, setMessage] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();
    const value = {
      product_name: "cream",
      amount: 20,
    };

    try {
      const res = await hosted(value);
      if (res?.status === 200) {
        window.location.href = res?.data?.url;
      }
    } catch (error) {
      console.log(error?.message);
    }
  };

  useEffect(() => {
    // Check to see if this is a redirect back from Checkout
    const query = new URLSearchParams(window.location.search);

    if (query.get("success")) {
      setMessage("Order placed! You will receive an email confirmation.");
    }

    if (query.get("canceled")) {
      setMessage(
        "Order canceled -- continue to shop around and checkout when you're ready."
      );
    }
  }, []);

  return message ? (
    <Message message={message} />
  ) : (
    <ProductDisplay handleSubmit={handleSubmit} />
  );
};
